import React from 'react';
import Button from 'react-bootstrap/Button';
import axios from 'axios';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';

function Password() {
  const changeState = useDispatch();

  var html = 'https://priority-server.herokuapp.com';
  const viewPassword = useSelector(state => state.password);

  const loginUnidade = useSelector(state => state.loginUnidade);
  const loginUsuario = useSelector(state => state.loginUsuario);
  const loginSetor = useSelector(state => state.loginSetor);
  var senha = '';

  // funo que busca um registro de login vlido no servidor.
  const updateSenha = () => {
    senha = document.getElementById('inputSenha').value;
  };

  const sendData = () => {
    if (senha == '') {
      swal({
        title: 'ERRO',
        text: 'Por favor, insira uma senha!',
        icon: 'warning',
        dangerMode: true
      });
      // gatilho para abertura do componente 'List' e atualizao das listas.
      changeState({
        type: 'TRIGGER_OFF',
        payloadUnidade: loginUnidade,
        payloadUsuario: loginUsuario,
        payloadSetor: loginSetor
      });
    } else {
      // atualizando o registro de login.
      var obj = {
        usuario: loginUsuario,
        senha: senha
      };
      axios.post(html + "/updatepassword/'" + loginUsuario + "'", obj);
      swal('SUCESSO', 'Senha alterada com sucesso!', 'success');
    }
  };

  // eslint-disable-next-line
  if (viewPassword == 1) {
    return (
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          height: '70vh'
        }}
      >
        <h1 style={{ color: 'red', fontWeight: 'bold' }}>
          USURIO: {loginUsuario}
        </h1>
        <h3 style={{ marginTop: 20, marginBottom: 10, fontSize: 16 }}>
          POR FAVOR, INSIRA A NOVA SENHA:
        </h3>
        <input
          onChange={() => updateSenha()}
          autoComplete="off"
          style={{
            width: 200,
            height: 50,
            textAlign: 'center',
            borderRadius: 5,
            border: 'none',
            outline: 'none',
            backgroundColor: '#E5E7E9',
            transition: '0.3s all'
          }}
          placeholder="NOVA SENHA..."
          type="password"
          id="inputSenha"
        ></input>
        <h3 style={{ marginTop: 10, marginBottom: 20, fontSize: 12 }}>
          PROCURE USAR PELO MENOS 6 CARACTERES, INCLUINDO NMEROS, LETRAS MAISCULAS E MINSCULAS.
        </h3>
        <Button
          onClick={() => sendData()}
          variant="secondary"
          style={{
            marginLeft: 5,
            marginTop: 20,
            marginBottom: 5
          }}
        >
          OK
        </Button>
      </div>
    );
  } else {
    return null;
  }
}
export default Password;
